.counter {
    background-color: #9e0cd1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    flex-wrap: nowrap;
    align-items: center;
    margin-top: 5px;
    box-shadow: 0 0 8px rgb(0 0 0 / 50%);
    border-radius: 10px;
}

.counter-btn {
    display: flex;
    padding: 2px;
    margin: 2px;
    box-shadow: 0 0 8px rgb(0 0 0 / 50%);
    border-radius: 5px;
    cursor: pointer;
    flex-direction: column;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-around;
    align-items: center;
}

.masMenos {
    width: 100%;
    height: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.agregar {
    border-radius: 5px;
    cursor: pointer;
    padding: 5px;
}

.counter-btn-btb {
    background-color: #9e0cd1;
    color: white;
    padding: 0 10px;
    border: none;
    font-size: 1.3rem;
    cursor: pointer;
}

.counter-display {
    color: white;
    padding: 0 10px;
    border: none;
    font-size: 1rem;
}

.add-btn {
    background-color: #9e0cd1;
    border: none;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    box-shadow: 0 0 8px 7px rgb(255 255 255 / 50%);
    border-radius: 5px;
    padding: 0.5rem;
    margin: 0.5rem
}